import { Component, OnDestroy, AfterViewInit } from '@angular/core';

import Quagga from 'quagga';

import content from '../content/en';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnDestroy, AfterViewInit {
  private currentMessageHolder: string;
  private currentUtterance = new SpeechSynthesisUtterance();

  cameraOn = false;
  barCodeFound = false;
  content = content;
  blink = false;
  blinkingInterval: number;
  cameraInitalized = false;
  scanning = false;
  shouldSpeak = true;

  set currentMessage(message) {
    this.currentMessageHolder = message;
    this.currentUtterance.text = this.strip(message);
  }

  get currentMessage() {
    return this.currentMessageHolder;
  }

  constructor() {
    this.currentUtterance.lang = 'en-US';

    this.currentUtterance.voice = window.speechSynthesis.getVoices().find((voice) => {
      return voice.voiceURI === 'Google UK English Male';
    });

    this.currentMessage = content.welcomeMessage;

    this.currentUtterance.onend = () => {
      clearInterval(this.blinkingInterval);
      this.blink = false;
    };

    window.addEventListener('beforeunload', (e) => {
      Quagga.stop();
      window.speechSynthesis.cancel();
    });
  }

  ngAfterViewInit(): void {
    Quagga.init({
      inputStream : {
        name : 'Live',
        type : 'LiveStream',
        target: document.querySelector('.camera-screen__content')
      },
      decoder : {
        readers : [
          'ean_reader',
          'ean_8_reader',
        ],
        multiple: true,
      },
      locator: {
        halfSample: true,
      }
    }, (err) => {
      if (err) {
        console.log(err);
        return;
      }
    });

    Quagga.onDetected((data) => {
      data.forEach(code => {
        const codeValue = code.codeResult.code;
        if ( content.barcodes[codeValue] ) {
          this.scanning = false;
          const message = `<h1>${content.barcodes[codeValue].name}</h1><p>${content.barcodes[codeValue].desc}<p>`;
          if (this.currentMessage !== message) {
            this.currentMessage = message;
            this.startSpeach();
          }
          this.barCodeFound = true;
        }
      });
    });

    this.startSpeach();
  }

  ngOnDestroy(): void {
    window.speechSynthesis.cancel();
  }

  onSpeakerClick() {
    this.shouldSpeak = !this.shouldSpeak;
    if (this.shouldSpeak) {
      this.startSpeach();
    } else {
      window.speechSynthesis.cancel();
    }
  }

  onStartScan() {
    this.scanning = true;
    this.barCodeFound = false;
    Quagga.start();
  }

  private startSpeach() {
    if (!this.shouldSpeak) {
      return;
    }
    window.speechSynthesis.cancel();
    this.blinkingInterval = setInterval(() => {
      this.blink = !this.blink;
    }, (Math.random() * 300) + 200);
    setTimeout(() => {
      window.speechSynthesis.speak(this.currentUtterance);
    }, 150);
  }

  private strip(message) {
    const doc = new DOMParser().parseFromString(message, 'text/html');
    return doc.body.textContent || '';
  }

}
