import { Component, OnInit, Input, ElementRef } from '@angular/core';
import { coerceBooleanProperty } from '@angular/cdk/coercion';

interface CircleStyle {
  'background-color': string;
  width: string;
  height: string;
  border?: string;
  boxShadow?: string;
  fontSize?: string;
}

@Component({
  selector: 'app-circle',
  templateUrl: './circle.component.html',
  styleUrls: ['./circle.component.scss']
})
export class CircleComponent implements OnInit {
  @Input() color: string;
  @Input() size: number;
  lighterColor: string;
  darkerColor: string;
  borderColor: string;
  private onHidden: boolean;

  @Input()
  set on(value: any) {
    this.onHidden = coerceBooleanProperty(value);
  }
  get on() {
    return this.onHidden;
  }

  constructor(private elRef: ElementRef) { }

  ngOnInit() {
    this.lighterColor = this.adjust(this.color, 20);
    this.darkerColor = this.adjust(this.color, -20);
    this.borderColor = this.adjust(this.color, -80);
  }

  getStyle(color: string, size: string, border?: string, on?: boolean) {
    const style: CircleStyle = {
      'background-color': color,
      width: `${size}px`,
      height: `${size}px`,
    };
    if (border) {
      style.border = `1px solid ${border}`;
    }
    if (on) {
      style.fontSize = `${size}px`;
      style.boxShadow = `0px 0px .8em .03em ${this.lighterColor}`;
    }
    return style;
  }

  private adjust(color, amount): string {
    return '#' + color.replace(/^#/, '').replace(
      /../g, colorTemp => ('0' + Math.min(255, Math.max(0, parseInt(colorTemp, 16) + amount)).toString(16)).substr(-2));
  }

}
